<template>
  <div id="app">
    <el-button type="primary" class="kai-test" @click="changeHide">凯哥练习专用</el-button>
    <root v-if="isTest"/>
    <div id="test" v-if="!isTest">
      <iframe src="./js-test/index.html" width="100%" height="100%" ref="fIframe"></iframe>
    </div>
  </div>
</template>

<script>
import root from './components/root'
export default {
  name: 'App',
  components: {
    root
  },
  data(){
    return {
      isTest: true
    }
  },
  methods: {
    changeHide(){
      this.isTest = false
    }
  },
  mounted(){
  }
}
</script>

<style lang="less">
#app {
  position: relative;
  color: #2c3e50;
  .kai-test{
    position: absolute;
    top: 8px;
    right: 10px;
  }
}
</style>



